<template>
  <div class="search">
    <van-nav-bar title="搜索" class="van-head" left-arrow @click-left="back">
    </van-nav-bar>
    <div class="search-input clearfix">
      <van-search placeholder="请输入搜索关键词" class="search-input-box fl" v-model="value" @search="getSearchData"/>
      <div class="fl search-input-bt" @click="getSearchData">确定</div>
    </div>
    <div class="search-box">
        <div class="clearfix search-hot-box">
            <div class="fl">全网热搜</div>
            <div class="fr clearfix"  @click="sake">
                <div class="fl"><van-icon name="replay" size="18" /></div>
                <div class="fl">换一换</div>
            </div>
        </div>
        <div class="clearfix">
          <div class="fl search-bar-tilte" v-for="(item,index) in HotData" :key="index" @click="getBookDetial(item.book)">{{item.word}}</div>
        </div>

    </div>
    <div class="search-item">
      <div class="search-item-list">
        <div class="search-list-title" @click="getBookDetial(item._id)" v-for="(item,index) in searchData" :key="index">{{item.title}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import '../assets/css/search.less'
export default {
  data(){
    return{
      // 搜索数据
      searchData:[],
      HotData:[],
      // 搜索热搜
      value:'',
      index: 0
    };
  },
  created(){
    // this.getSearchData();
    this.getHotData();
  },
  
  methods:{
    //返回
    back(){
        this.$router.go(-1);
    },
    toggleTabBar(name) {
      this.$router.push({name:name});
    },
    //搜索书籍
    getSearchData(){
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      let keyword = this.value;
      this.axios({
        method:"GET",
        url:"http://novel.kele8.cn/search?keyword="+ keyword
      }).then((result) =>{
          this.$toast.clear();
          if(result.status == 200){
            this.searchData = result.data.books;
          }
      })
    },
    // 热门搜索
    getHotData(){
       this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method:'GET',
        url:'http://novel.kele8.cn/hot-books'
      }).then((result) =>{
          this.$toast.clear();
        console.log(result);

          if(result.status == 200){
            this.HotData = result.data.newHotWords.splice(this.index,4);
            console.log(this.HotData)
            // this.HotData = result.data.searchHotWords.splice(this.index,6);
          }
          
      })
    },
    sake(){
      this.index += 4 ;
      this.index = this.index >= 7 ? 0 : this.index;
      this.getHotData();
    },

     getBookDetial(id) {
      this.$router.push({ name: "Detail", query: { id: id } });
    },

  }
};
</script>

<style lang="less" scoped>
body{
  background-color: #fff;
}
</style>